












































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import { UserModule } from '@/store/modules/user'
import { Route } from 'vue-router'
import { Form as ElForm } from 'element-ui'

const AV = require('leancloud-storage')

@Component
export default class Register extends Vue {
  private registerForm = {
    username: '',
    password: '',
    confirm_password: '',
    work: '',
    level: ''
  };

  private validateUsername = (rule: any, value: string, callback: any) => {
    if (value.length < 5) {
      callback(new Error('用户名不能小于5位'))
    } else {
      callback()
    }
  }
  private validatePass = (rule: any, value: string, callback: any) => {
    if (value.length < 5) {
      callback(new Error('密码不能小于5位'))
    } else {
      callback()
    }
  }

  private validateConfirmPass = (rule: any, value: string, callback: any) => {
    if (value != this.registerForm.password) {
      callback(new Error('与输入密码不一致'))
    } else {
      callback()
    }
  }
  private registerRules = {
    username: [{ required: true, trigger: 'blur', validator: this.validateUsername }],
    password: [{ required: true, trigger: 'blur', validator: this.validatePass }],
    confirm_password: [{ required: true, trigger: 'blur', validator: this.validateConfirmPass }]
  };
  private loading = false;
  private redirect: string | undefined = undefined;
  private pwdType = 'password';
  private showPwd() {
    if (this.pwdType === 'password') {
      this.pwdType = ''
    } else {
      this.pwdType = 'password'
    }
  }
  @Watch('$route', { immediate: true })
  private OnRouteChange(route: Route) {
    // TODO: remove the "as string" hack after v4 release for vue-router
    // See https://github.com/vuejs/vue-router/pull/2050 for details
    this.redirect = route.query && route.query.redirect as string
  }

  private loginPage() {
    this.$router.push({ path: '/login' })
  }

  private handleCopyUsrInfo() {
    var Forms = AV.Object.extend('UsrInfo')
    var formObject = new Forms()
    formObject.save({
      username: this.registerForm.username,
      password: this.registerForm.password,
      job: this.registerForm.work,
      title: this.registerForm.level
    }).then((object:any) => {
    }).catch(() => {
    })
  }

  private handleRegister() {
    (this.$refs.registerForm as ElForm).validate((valid: boolean) => {
      if (valid) {
        this.loading = true
        AV.User.signUp(this.registerForm.username, this.registerForm.password, { 'job': this.registerForm.work, 'title': this.registerForm.level, 'auth': 'D' }).then((user:any) => {
          this.loading = false
          this.handleCopyUsrInfo()
          this.$message('创建用户成功!')
          this.registerForm.username = this.registerForm.password = this.registerForm.level = this.registerForm.work = this.registerForm.confirm_password = ''
          // this.loginPage()
        }).catch(() => {
          this.loading = false
          this.$message('创建用户失败!')
        })
      }
    })
  }
}
